<template>
  <div class="mainwrap">
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
      </div>
      <h1>Order Summary</h1>
      <div class="chartWrap"></div>
    </section>

    <section class="orderSummaryWrap">
      <div class="address item-box">
        <p class="tittle">Delivery Address</p>
        <template v-if="!noAdress">
          <p class="user" @click="redirect('/myAddress?action=select')">
            <span> {{ orderData.address.userName }} </span>
            <span> NO.+971 {{ orderData.address.mobile }} </span>
            <span class="tag"> Home </span>
          </p>
        </template>
        <div class="box-content" @click="redirect('/myAddress?action=select')">
          <div v-if="!noAdress">
            <p class="two-line-ellipsis">{{ orderData.address.address }}</p>
          </div>
          <div v-if="noAdress" @click="addressGo">
            <span class="iconfont"> &#xe601; </span>
            <span> Add shipping address </span>
          </div>
          <div class="rightChange" :class="{ rightArrow: noAdress }">
            <span class="iconfont">
              {{ noAdress ? "&#xe93d;" : "&#xe75d;" }}</span
            >
          </div>
        </div>
      </div>

      <section class="proList">
        <div class="item-box" v-for="(item, index) in initProList" :key="index">
          <div class="tittleWrap">
            <p class="tittle">
              {{ item.shipmentTitle }}
            </p>
            <div class="deliveryTime">
              <p class="red">
                delivered in {{ item.deliveredDays }} business days
              </p>
              <div class="chooseTime">
                <p class="outer" @click="showAnyTime(item, index)">
                  <span>
                    {{
                      !!item.timeLabel
                        ? item.timeLabel
                        : "Preferred delivery time"
                    }}
                  </span>
                  <span class="iconfont arrowDown"> &#xe671; </span>
                </p>
                <div class="timeList" v-show="item.timeShow">
                  <p
                    v-for="(timeItem, timeIdx) in item.deliveryTimeList"
                    @click="changeTime(item, timeItem, timeIdx)"
                    :class="{ red: item.timeVal == timeIdx }"
                    :key="timeIdx"
                  >
                    {{ timeItem }}
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div
            class="box-content clearfix"
            v-for="(pro, i) in item.skuList"
            :key="i"
          >
            <div class="imgWrap">
              <img :src="pro.img0" class="img" />
              <p class="cutOff">{{ item.cutOff }}</p>
            </div>
            <div class="rightMain" @click="$router.push({
                    path: '/proDetail',
                    query: { spuCode: pro.spuCode}
                  })">
              <div class="nameWrap">
                <div class="name">{{ pro.name }}</div>
                <div class="num">
                  <span class="iconfont"> &#xe619; </span>
                  <span> {{ pro.num }} </span>
                </div>
              </div>
              <div class="proDownWrap">
                <div class="sizeWrap">
                  <p class="size">
                    {{ getAttributes(pro.attributeList) }}
                  </p>
                  <p class="shipping" v-if="item.shipping">
                    Shipping: {{ pro.shipping }} AED
                  </p>
                </div>
                <div>
                  <p class="price">{{ pro.mallPrice }} AED</p>
                  <p class="priceDel">{{ pro.merchantPrice }} AED</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <div class="item-box useCoupon" @click="counponShow" v-if="couponList.length>0">
        <p class="tittle">Choose Coupon</p>
        <div class="box-content">
          <div>
            <span class="iconfont radio" :class="{ red: chooseNum }">
              {{ chooseNum ? "&#xe63a;" : "&#xe608;" }}
            </span>
            <span class="value" > {{ chooseNum }} Selected </span>
          </div>
          <div><span class="iconfont arrowDown"> &#xe671; </span></div>
        </div>
      </div>

      <div class="item-box total">
        <p class="tittle">Payment summary</p>
        <div class="box-content">
          <p class="row">
            <label> Sub Total: </label>
            <span> {{ orderData.subTotal }} AED </span>
          </p>
          <p class="row">
            <label> Shipping: </label>
            <span> {{ orderData.shipping }} AED </span>
          </p>
          <!-- <p class="row"> <label> Import fees deposit: </label> <span> 896.25 AED </span></p> -->
          <p class="row">
            <label> Coupon: </label> <span> - {{ couponAmount }} AED</span>
          </p>
        </div>
      </div>

      <section class="downWrap">
        <div class="tips">
          <div class="red">{{ orderData.messae }}</div>
          <div class="gray" v-if="false">
            To gather up a list <span class="iconfont"> &#xe93d; </span>
          </div>
        </div>
        <div class="downBtn">
          <div class="rightOperate">
            <span class="black"> Grand Total: </span>
            <span class="price"> {{ grandTotal }} AED </span>
            <van-button type="danger" @click="payMentGo">Proceed(1)</van-button>
          </div>
        </div>
      </section>
    </section>

    <van-popup
      v-model="cuponShow"
      position="bottom"
      @click-overlay="cuponShow = false"
    >
      <div class="couponList">
        <a
          class="iconfont close"
          href="javascript:void(0)"
          @click="cuponShow = false"
        >
          &#xe66c;
        </a>
        <ul class="clearfix">
          <li v-for="(item, index) in couponList" :key="index">
            <span
              class="iconfont radio"
              :class="{ red: selectIndex.indexOf(index) > -1 }"
              @click="selectCoupon(item, index)"
            >
              {{ selectIndex.indexOf(index) > -1 ? "&#xe63a;" : "&#xe608;" }}
            </span>
            <div>
              <p class="name">{{ item.name }}</p>
              <p class="row2">
                <span class="price"> {{ item.couponAmount }} </span>
                <span class="date">
                  {{ item.beginTime }}-{{ item.endTime }}
                </span>
              </p>
            </div>
          </li>
        </ul>
        <div class="btnWrap">
          <van-button type="danger" @click="confirmCoupon" size="large"
            >Confirm</van-button
          >
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { checkOrder, submitOrder } from "@/api/order.js";
import {mapGetters} from 'vuex';
export default {
  name: "orderSummary",
  data() {
    return {
      grandTotal: "",
      selectIndex: [],
      couponAmount: 0,
      ids: JSON.parse(this.$route.query.ids),
      isVIP: 1,
      cuponShow: false,
      orderData: {},
      chooseNum: "",
      couponList: [],
      hisCouponList: [],
      initCouponList: [],
      noAdress: true,
      initProList: []
    };
  },
  computed: {...mapGetters(['selectAddress'])},
  mounted() {
    this.init();
    // this.initAddress();
  },
  methods: {
    init() {
      checkOrder(this.ids).then(res => {
        this.orderData = res.data.result;
        this.initProList = res.data.result.shipmentList;
        if (this.initProList.length > 0) {
          this.initProList.forEach(el => {
            el.timeVal = 0;
            el.timeLabel = el.deliveryTimeList[0];
          });
        }
        this.couponList = res.data.result.couponList.filter(t=>{return t.canUse!=0;});
        if (this.couponList.length > 0) {
          this.selectIndex.push(0);
          this.couponAmount = this.couponList[0].couponAmount;
          this.chooseNum = 1;
        }
        this.calTotal();
        if (this.orderData.address) {
          this.noAdress = false;
        }
        if(this.selectAddress){
          this.orderData.address={
            address:this.selectAddress.countryName+' '+this.selectAddress.provinceName+' '+this.selectAddress.cityName+" "+this.selectAddress.street,
            addressId:this.selectAddress.id,
            addressType:this.selectAddress.addressType,
            landlineNumber:this.selectAddress.landlineNumber,
            landmark:this.selectAddress.landmark,
            mobile:this.selectAddress.mobile,
            userName:this.selectAddress.lastName+' '+this.selectAddress.firstName,
          };
        }
      });
    },
    //我的地址
    initAddress() {
      getAddress().then(res => {});
    },
    initProData() {
      // this.proList.map((val)=>{
      //   this.initProList.push(JSON.parse(JSON.stringify(Object.assign(val,{"timeShow":false}))))
      // })
    },
    //选择优惠劵
    selectCoupon(item, i) {
      if (item.use == 0) {
        return false;
      }
      // if (this.selectIndex.indexOf(i) > -1) {
      //   this.selectIndex.splice(this.selectIndex.indexOf(i), 1);
      // } else {
      //   this.selectIndex.push(i);
      // }
      if (this.selectIndex.indexOf(i) > -1) {
        this.selectIndex.splice(0, 1);
      } else {
        this.selectIndex.splice(0, 1);
        this.selectIndex.push(i);
      }
      item.check = !item.check;
      this.calTotal();
    },
    changeTime(item, timeItem, timeIdx) {
      this.$set(item, "timeVal", timeIdx);
      this.$set(item, "timeLabel", timeItem);
      this.$set(item, "timeShow", false);
    },
    counponShow() {
      if(this.couponList.length==0){
        
      }else{
        this.cuponShow = true;
      }
    },
    calTotal() {
      let res =
        this.orderData.subTotal * 1 +
        this.orderData.shipping * 1 -
        (this.couponAmount * 1 || 0);
      this.grandTotal = res > 0 ? res.toFixed(2) : "0.01";
    },
    // 确定选择
    confirmCoupon() {
      this.chooseNum = 0;
      this.couponAmount = 0;
      if (this.selectIndex.length > 0) {
        this.selectIndex.forEach(e => {
          this.chooseNum++;
          this.couponAmount += this.couponList[e].couponAmount * 1;
        });
      }
      this.cuponShow = false;
      this.calTotal();
    },
    goBack() {
      this.$router.back(-1);
    },
    addressGo() {
      this.$router.push("/addAddress?action=select");
    },
    getAttributes(arr) {
      return arr.map(t => t.optionName).join(";");
    },
    showAnyTime(item) {
      this.$set(item, "timeShow", !item.timeShow);
    },
    //跳转支付页面
    payMentGo() {
      if (!this.orderData.address) {
        this.$toast("Add address");
        return false;
      }

      let expectedDeliveryTime = [];
      this.initProList.forEach(el => {
        expectedDeliveryTime.push(el.shipmentFrom + "~" + el.timeLabel);
      });

      let params = {
        grandTotal: this.grandTotal,
        couponId:
          this.selectIndex.length > 0
            ? this.couponList[this.selectIndex[0]].couponId
            : "",
        ids: this.ids.join(","),
        addressId: this.orderData.address.addressId,
        expectedDeliveryTime: expectedDeliveryTime.join(",")
      };

      //payMent?grandTotal=4599.00&couponId=4&ids=27_010100110000009678&addressId=4&expectedDeliveryTime=china~Any%20Daytime
      submitOrder(params).then(res => {
        this.$router.push({
          path: "/payMent",
          query: res.data.result
        });
        this.bus.$emit('loadIndexData');
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/order.scss";
</style>



